  getAsal(data, kiriman = false){
    var asal='';
    if(!kiriman){
      if(parseInt(data.asal) === 1){
        asal = 'Kelurahan '+data.Nm_Kel;
      }else if(parseInt(data.asal) === 2){
        asal = 'Kecamatan '+data.Nm_Kec;
      }
    }else{
      if(parseInt(data.asal) === 0){
        asal = 'Kecamatan '+data.Nm_Kec;
      }else{
        asal = 'Kelurahan '+data.Nm_Kel;
      }
    }
    return (<p>{asal}</p>);
  }

